<template>

    <v-layout class="pa-3">
        <v-flex xs12>
            <v-btn round :color="colors[Math.floor(Math.random()*4)]" dark v-for="i in list" :key="i.id">#{{i.name}}</v-btn>
        </v-flex>
    
    </v-layout>

</template>

<script>
import axios from 'axios'


export default {
    data(){
        return{
        list: ['Design','Arts','Play','FrontEnd','BackEnd','React','Angular'],
        colors: ['orange','red','blue','teal','green'],
        }
    },
    created(){
        axios.get('http://localhost:8000/categorylist/').then(res=>{this.list=res.data})
    }
}


</script>
